@import url(https://fonts.googleapis.com/css2?family=Fondamento&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
  background-color: rgb(49,49,49);
}
.map{height:700px;width:700px;margin:0 auto;background-position:center;background-size:contain;background-repeat:no-repeat}.map__title{text-align:center}@media (max-width: 1050px){.map{height:500px;width:500px}}@media (max-width: 800px){.map{max-width:100%}}

.map-container{overflow:hidden;text-align:center}.map-container__title{color:#d8bb7d;font-size:2rem;font-weight:700}

.target-item{text-align:left;border-top:none;cursor:pointer;color:white;padding:5px;font-size:.9em}.target-item:hover{background-color:#282828}.target-item__container{margin:10px;width:47%}.target-item__subcontainer{position:relative;border-top:1px solid black}.target-item__title{cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;padding:5px;margin:0;border:1px solid black;border-radius:5px;box-shadow:0 0 3px black;color:#d8bb7d;font-weight:500;text-shadow:2px 2px 2px black;background:linear-gradient(0deg,#464646 0%,#313131 40%, #5a5a5a 60%)}.target-item__title:hover{background:linear-gradient(0deg,#323232 0%,#1e1e1e 40%, #464646 60%);box-shadow:0 0 0px black}.target-item__name{font-weight:500;font-size:1.2em}.target-item p{margin:5px}.x-button{background-color:transparent;border:none;border-radius:3px;cursor:pointer;font-size:2em;font-weight:500}.x-button:hover{color:white}.x-button__container{position:absolute;top:20%;right:5px}@media (max-width: 1400px){.target-item__container{width:100%}}

.target-container{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;color:black}

.rank-item{margin-bottom:10px}.rank-item__title{display:block;border:2px solid green;color:#d8bb7d;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;padding:5px;font-size:1.25rem;font-weight:500;border:1px solid black;border-radius:5px;box-shadow:0 0 3px black;text-shadow:2px 2px 2px black;background:linear-gradient(0deg,#464646 0%,#313131 40%, #5a5a5a 60%)}.rank-item__title:hover{background:linear-gradient(0deg,#323232 0%,#1e1e1e 40%, #464646 60%);box-shadow:0 0 0px black}.is-nav-open{-webkit-transform:rotate(90deg);transform:rotate(90deg)}

.rank-container{padding:0px 10px 10px 10px;text-align:center;max-height:85vh;overflow-y:auto;font-family:sans-serif}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{background:transparent}::-webkit-scrollbar-thumb{background:#9F2637}::-webkit-scrollbar-thumb:hover{background:#4d121b}

.dropdown-header{text-align:center;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.dropdown-header__title{display:inline-block;text-transform:uppercase;color:#d8bb7d;cursor:pointer;width:500px;border-bottom:2px solid #d8bb7d}.dropdown-header__title:hover{color:#e4d0a4}

.dropdown-item{font-size:1.75em;padding:10px;width:500px;background-color:#191919;border-left:5px solid black;border-right:5px solid black;display:flex;justify-content:center}.dropdown-item__icon{height:40px}.dropdown-item__name{text-transform:uppercase;color:#d8bb7d;cursor:pointer}.dropdown-item__name:hover{color:#f0e4cb}

.dropdown-list{display:flex;flex-direction:column;justify-content:center;align-items:center}.dropdown-list__container{position:absolute;margin-left:auto;margin-right:auto;left:0;right:0;text-align:center;z-index:1}

.homepage__container{margin:0 auto;padding-top:20px;font-family:'Fondamento', sans-serif;color:#9F2637;position:absolute;left:0;right:0;bottom:0;top:0}.homepage__dropdown-section{display:flex;justify-content:center}.homepage__dropdown-section-text{display:inline-block}.homepage__logo{width:400px;-webkit-animation:loading 60s linear infinite;animation:loading 60s linear infinite;-webkit-animation-delay:5s;animation-delay:5s}@-webkit-keyframes loading{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}5%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes loading{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}5%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.homepage__row{display:flex;justify-content:center}.homepage__map-section{width:700px}.homepage__no-selection{display:flex;justify-content:center;align-items:center;height:80vh}.homepage__rank-section{width:900px}@media (max-width: 800px){.homepage__row{flex-direction:column;align-items:center}.homepage__map-section{width:100%;height:auto}.homepage__rank-section{width:100%}}

